.skeleton-chart {
  height: inherit;
  position: relative;
  width: 100%;
}

.skeleton-chart::after {
  background-image: url("../../../images/skeleton-chart.svg");
  background-position: 0px bottom;
  background-repeat: no-repeat;
  background-size: cover;
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  width: 100%;
}

.skeleton-text {
  animation: loading-skeleton 1s ease-out 0.15s infinite alternate;
  background: #eaedf1;
  height: 24px;
  opacity: 0;
}

.skeleton-text::after {
  background-repeat: no-repeat;
  content: "";
  display: block;
  height: 100%;
  width: 100%;
}

.co-inventory-card__item {
  align-items: center;
  display: flex;
  font-size: 1rem;
  justify-content: space-between;
  line-height: 1.5rem;
  padding: 1rem 0 0 0;
}

.co-inventory-card__item-status {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
}

.co-inventory-card__item-title {
  display: flex;
  margin-right: 0.5rem;
}

.co-inventory-card__status {
  align-items: center;
  display: flex;
  flex-shrink: 0;
  margin-left: 1.8rem;
}

.co-inventory-card__status-icon--question {
  color: #d2d2d2;
}

.co-inventory-card__status-icon--progress {
  color: #737679;
}

.co-inventory-card__status-text {
  margin-left: 0.25rem;
}

.co-inventory-card__accordion.pf-c-accordion {
  border-bottom: 1px solid #d2d2d2;
  box-shadow: none;
  padding: 0;
}

.co-inventory-card__accordion.pf-c-accordion h5 {
  font-size: var(--pf-c-card__body--FontSize);
  font-weight: var(--pf-global--FontWeight--normal);
  margin: 0;
}

.co-inventory-card__accordion.pf-c-accordion .co-inventory-card__accordion-toggle.pf-c-accordion__toggle {
  border-left: none;
  padding: 0 var(--pf-c-card--child--PaddingRight) 0 0;
}

.co-inventory-card__accordion.pf-c-accordion .co-inventory-card__accordion-toggle.pf-c-accordion__toggle .pf-c-accordion__toggle-text {
  width: 100%;
}

.co-inventory-card__accordion.pf-c-accordion .co-inventory-card__accordion-body.pf-c-accordion__expanded-content {
  border-left: none;
}

.co-inventory-card__accordion.pf-c-accordion .co-inventory-card__accordion-body.pf-c-accordion__expanded-content .pf-c-accordion__expanded-content-body {
  padding: 1rem var(--pf-c-card--child--PaddingRight) 1rem var(--pf-c-card--child--PaddingLeft);
}

.skeleton-inventory {
  animation: loading-skeleton 1s ease-out 0.15s infinite alternate;
  background: #eaedf1;
  height: 20px;
  width: 20px;
  opacity: 0;
  margin-right: 0.5rem;
}

.skeleton-inventory::after {
  background-repeat: no-repeat;
  content: "";
  display: block;
  height: 100%;
  width: 100%;
}

