.co-dashboard-card {
  margin: 0;
}

.co-dashboard-card--gradient {
  position: relative;
}

.co-dashboard-card--gradient:after {
  background: linear-gradient(rgba(255,255,255,0), var(--pf-global--BackgroundColor--100));
  bottom: 0;
  content: '';
  left: 0;
  min-height: 2rem;
  pointer-events: none;
  position: absolute;
  width: calc(100% - var(--pf-global--spacer--lg));
}

.co-dashboard-card__body--no-padding {
  margin-bottom: calc(-1 * var(--pf-c-card--child--PaddingBottom));
  margin-left: calc(-1 * var(--pf-c-card--child--PaddingLeft));
  margin-right: calc(-1 * var(--pf-c-card--child--PaddingRight));
}

.co-dashboard-card__body--top-margin {
  margin-top: 1rem;
}

.co-dashboard-card__body--bottom-margin {
  margin-bottom: 1rem;
}

.co-dashboard-card__button-link,
.co-dashboard-card__link {
  line-height: 19px !important;
}

.co-dashboard-card__header {
  align-items: center;
  border-bottom: 1px solid #d2d2d2;
  display: flex;
  justify-content: space-between;
  padding-bottom: var(--pf-c-card__header--not-last-child--PaddingBottom);
  padding-left: var(--pf-c-card--child--PaddingLeft);
  padding-right: var(--pf-c-card--child--PaddingRight);
  padding-top: var(--pf-c-card--first-child--PaddingTop);
}

.co-dashboard-card__title {
  font-size: 1rem;
  line-height: 19px;
  margin: 0;
}

.co-dashboard-icon {
  font-size: 1.2rem;
}

.co-dashboard-text--small {
  font-size: 0.875rem;
}

