.skeleton-chart {
  height: inherit;
  position: relative;
  width: 100%;
}

.skeleton-chart::after {
  background-image: url("../../../images/skeleton-chart.svg");
  background-position: 0px bottom;
  background-repeat: no-repeat;
  background-size: cover;
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  width: 100%;
}

.skeleton-text {
  animation: loading-skeleton 1s ease-out 0.15s infinite alternate;
  background: #eaedf1;
  height: 24px;
  opacity: 0;
}

.skeleton-text::after {
  background-repeat: no-repeat;
  content: "";
  display: block;
  height: 100%;
  width: 100%;
}

:root {
  --pf-chart-global--FontFamily: $font-family-base;
  --pf-global--FontSize--md: $font-size-base;
  --pf-global--FontSize--sm: 13px;
}

.co-activity-card__body {
  max-height: 30rem;
  min-height: 10rem;
  overflow-x: hidden;
  overflow-y: auto;
}

.co-activity-card__ongoing-body .co-activity-item__ongoing:last-of-type {
  border-bottom: none;
}

.co-activity-card__ongoing-title {
  border-bottom: solid 1px #d2d2d2;
  padding: 0.2rem var(--pf-c-card--child--PaddingRight) 0.2rem var(--pf-c-card--child--PaddingLeft);
}

.co-activity-card__recent-more-link {
  padding: 0 var(--pf-c-card--child--PaddingRight) var(--pf-c-card--child--PaddingBottom) var(--pf-c-card--child--PaddingLeft);
}

.co-activity-card__recent-accordion.pf-c-accordion {
  box-shadow: none;
  padding-bottom: 0;
  padding: .75rem 0;
}

.co-activity-card__recent-accordion.pf-c-accordion h5 {
  font-family: var(--pf-global--FontFamily--sans-serif);
  font-size: var(--pf-c-card__body--FontSize);
  margin: 0;
}

.co-activity-card__recent-accordion.pf-c-accordion .co-recent-item__content .pf-c-accordion__expanded-content-body {
  padding-left: var(--pf-c-card--child--PaddingLeft);
  padding-right: var(--pf-c-card--child--PaddingRight);
}

.co-activity-card__recent-accordion.pf-c-accordion .co-recent-item__toggle.pf-c-accordion__toggle {
  line-height: 18px;
  padding-left: var(--pf-c-card--child--PaddingLeft);
  padding-right: var(--pf-c-card--child--PaddingRight);
}

.co-activity-card__recent-accordion.pf-c-accordion .co-recent-item--warning {
  border-left-color: var(--pf-global--warning-color--100);
}

.co-activity-card__recent-accordion.pf-c-accordion .pf-c-accordion__toggle .pf-c-accordion__toggle-text,
.co-activity-card__recent-accordion.pf-c-accordion .pf-c-accordion__toggle:active .pf-c-accordion__toggle-text,
.co-activity-card__recent-accordion.pf-c-accordion .pf-c-accordion__toggle:focus .pf-c-accordion__toggle-text {
  font-weight: var(--pf-global--FontWeight--normal) !important;
}

.co-activity-card__recent-actions svg {
  vertical-align: -0.125rem !important;
}

.co-activity-card__recent-list {
  padding-bottom: 0 !important;
}

.co-activity-card__recent-title {
  border-top: solid 1px #d2d2d2;
  border-bottom: solid 1px #d2d2d2;
  display: flex;
  justify-content: space-between;
  padding: 0.2rem var(--pf-c-card--child--PaddingRight) 0.2rem var(--pf-c-card--child--PaddingLeft);
}

.co-activity-item {
  display: flex;
}

.co-activity-item__ongoing {
  margin: 1rem var(--pf-c-card--child--PaddingRight) 1rem var(--pf-c-card--child--PaddingLeft);
}

.co-activity-item__progress.pf-c-progress {
  grid-gap: 0;
  padding-bottom: 0.2rem;
}

.co-activity-item__icon {
  padding-right: var(--pf-global--spacer--xs);
}

.co-recent-item__content-header {
  display: flex;
  justify-content: space-between;
  white-space: pre;
}

.co-recent-item__content-message {
  color: var(--pf-global--Color--100);
  margin-top: 0.5rem;
  word-wrap: break-word;
}

.co-recent-item__content-resourcelink {
  display: block;
  flex: 2 0 0;
  white-space: nowrap;
}

.co-recent-item__content-resourcelink .co-resource-item__resource-name {
  font-size: 14px;
  text-overflow: ellipsis;
  overflow: hidden;
}

.co-recent-item__icon--warning {
  align-items: center;
  display: flex;
  margin-right: 0.3rem;
  margin-top: 0;
  flex: 0 0 auto;
}

.co-recent-item__title {
  display: flex;
  align-items: center;
}

.co-recent-item__title-message {
  align-items: center;
  display: flex;
  overflow: hidden;
}

.co-recent-item__title-message-text {
  overflow: hidden;
  text-overflow: ellipsis;
}

.co-recent-item__title-timestamp {
  min-width: 45px;
  padding-right: 0.3rem;
  text-align: left;
}

.skeleton-activity__dashboard {
  animation: loading-skeleton 1s ease-out 0.15s infinite alternate;
  height: 24px;
  margin-bottom: 1em;
  opacity: 0;
  width: 100%;
}

.skeleton-activity__dashboard::after {
  background-repeat: no-repeat;
  content: "";
  display: block;
  height: 100%;
  width: 100%;
}

.skeleton-activity__dashboard:last-of-type {
  margin-bottom: 0;
}

.skeleton-activity__dashboard::after {
  background-image: linear-gradient(#eaedf1 24px, transparent 0),radial-gradient(circle 12px at center, #eaedf1 100%, transparent 0);
  background-position: 45px 0,0 0;
  background-size: auto,24px 24px;
}

