.skeleton-chart {
  height: inherit;
  position: relative;
  width: 100%;
}

.skeleton-chart::after {
  background-image: url("../../../images/skeleton-chart.svg");
  background-position: 0px bottom;
  background-repeat: no-repeat;
  background-size: cover;
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  width: 100%;
}

.skeleton-text {
  animation: loading-skeleton 1s ease-out 0.15s infinite alternate;
  background: #eaedf1;
  height: 24px;
  opacity: 0;
}

.skeleton-text::after {
  background-repeat: no-repeat;
  content: "";
  display: block;
  height: 100%;
  width: 100%;
}

.co-resource-quota-card__chart {
  margin-bottom: 0;
  padding-top: var(--pf-global--spacer--md);
}

.co-resource-quota-card__item-title {
  margin-top: var(--pf-global--spacer--md);
}

.skeleton-quota {
  animation: loading-skeleton 1s ease-out 0.15s infinite alternate;
  background: #eaedf1;
  height: 30px;
  opacity: 0;
}

.skeleton-quota::after {
  background-repeat: no-repeat;
  content: "";
  display: block;
  height: 100%;
  width: 100%;
}

