/* OpenShift Logos Icon Font */
@font-face {
  font-family: 'openshift-logos-icon';
  src: url('fonts/openshift-logos-icon.ttf') format('truetype'), url('fonts/openshift-logos-icon.woff') format('woff'), url('fonts/openshift-logos-icon.svg#openshift-logos-icon') format('svg');
  font-weight: normal;
  font-style: normal;
}
.font-icon {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'openshift-logos-icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
