.ocs-drawer {
  position: relative;
  overflow: hidden;
  flex-grow: 0;
  flex-shrink: 0;
  transition: opacity 175ms ease-out, transform 225ms ease-out;
  box-shadow: var(--pf-global--BoxShadow--sm-top);
  display: flex;
  flex-direction: column;
}

.ocs-drawer__drag-handle {
  width: 100%;
  height: 6px;
  cursor: ns-resize;
  position: absolute;
  background-color: transparent;
}

.ocs-drawer__drag-handle:hover {
  background-color: var(--pf-global--palette--black-300);
}

.ocs-drawer__header {
  height: var(--pf-global--target-size--MinHeight);
  background-color: var(--pf-global--palette--black-200);
  display: flex;
  align-items: center;
  flex-shrink: 0;
}

.ocs-drawer__body {
  position: relative;
  flex-grow: 1;
  overflow: auto;
}

.ocs-drawer-appear {
  opacity: 0;
  transform: translatey(10%);
}

.ocs-drawer-appear-active {
  opacity: 1;
  transform: translatey(0);
}

.ocs-drawer-exit {
  opacity: 1;
  transform: translatey(0);
}

.ocs-drawer-exit-active {
  opacity: 0;
  transform: translatey(10%);
}

