:root {
  --pf-chart-global--FontFamily: $font-family-base;
  --pf-global--FontSize--md: $font-size-base;
  --pf-global--FontSize--sm: 13px;
}

.co-dashboard-body {
  background-color: #ededed;
  flex: 1 0 auto;
  padding: 15px;
}

@media (min-width: 768px) {
  .co-dashboard-body {
    padding: 30px;
  }
}

.co-dashboard-grid {
  grid-gap: 1rem;
}

